import React, { useEffect, useState } from 'react'

import TextInput from 'react-autocomplete-input';
import 'react-autocomplete-input/dist/bundle.css';
import 'react-dropdown/style.css';

import './RangeConv.css'

import { newDrugList } from '../data/drugs'

export default function RangeConv({ selectedDrug, result, onChangeInput, rangeInput, onResetInput }) {


    const chosenDrug = newDrugList.find(d => d.name === selectedDrug)
    const isDisabled = !chosenDrug?.hasOwnProperty('dose_lower')


    const handleChange = (content) => {
        onChangeInput(content.target.value)
    }

    const formatRange = () => {
        // const chosenDrug = newDrugList.find(d => d.name === selectedDrug)
        let endVal = ''
        // console.log(chosenDrug.dose_upper)
        if (!isDisabled) {
            if (chosenDrug.dose_upper) {
                endVal = `- ${chosenDrug.dose_upper}`
            }
            return `${chosenDrug.dose_lower} ${endVal}`
        }
        // return `${drugNum[0]}mg/${drugNum[1]}mL`
    }


    return (
        <div className='subcontainer'>
            {/* dose range */}
            {!isDisabled && <p className='test'>Dose: {formatRange()}mg/kg </p>}

            {/* weight of person */}
            <label id='children'>
                <span id='children'>hooman weight:</span>
                <input
                    type='text'
                    onChange={handleChange}
                    value={rangeInput} className="inputBox"
                    disabled={isDisabled}
                    style={{ backgroundColor: isDisabled && 'red' }}
                />
                <button
                    onClick={() => onResetInput()}
                    disabled={isDisabled}
                    style={{ color: isDisabled && 'red' }}
                >x</button>
            </label>


            {result && (<div className="resultContainer2">
                {result.map(r => (
                    <div className='rangeResults'>
                        <span className='test'>{r.label}</span>
                        <ul id='result' 
                        key={Math.random()} 
                        style={{backgroundColor: r.warning? 'red' : ''}}>
                            {r.weight.toFixed(2)}mg = {r.vol.toFixed(2)}mL
                        </ul>
                    </div>
                ))}
            </div>)}
        </div>
    );
}
