import React from 'react'
import 'react-dropdown/style.css';
// import './DirectConv.css'

export default function DirectConv({onClearInputs, onInputChange, mgInput, mLInput}) {

    console.log(mgInput)


    return (
        <div className='subcontainer'>
            <div className='doseBar'>
                <span>Dose:</span>
                <span className='sub'>
                    <input type='text'
                        onChange={(content) => onInputChange('mg', content)}
                        // value={mgInput == '' ? '' : mgInput}
                        value={mgInput}
                        className="inputBox" />
                    mg
                </span>
                <span className='sub'>
                    <input type='text'
                        onChange={(content) => onInputChange('mL', content)}
                        // value={mLInput == '' ? '' : mLInput}
                        value={mLInput}
                        className="inputBox" />
                    mL
                </span>
            </div>
            <button onClick={onClearInputs} style={{marginTop: '15px'}}>clear</button>
        </div>
    )
}
